<template>
<!-- 轮播 -->
  <div id="swipercom">
    <div class="swiper-container" id="swiperindex">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, index) in imgs" :key="index">
          <img :src="item.pic" />
        </div>
      </div>
      <!-- 如果需要分页器 -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
// import style (<= Swiper 5.x)
import "swiper/css/swiper.css";
import Swiper from "swiper";
import axios from "axios";
import { getBanner } from "@/api/index";
export default {
  data() {
    return {
      imgs: [
        { pic: require("../assets/img/swiper1.jpg") },
        { pic: require("../assets/img/swiper2.jpg") },
        { pic: require("../assets/img/swiper3.jpg") },
        // {pic:require("../assets/img/swiper4.jpg")},
        // {pic:require("../assets/img/swiper5.jpg")},
        // {pic:require("../assets/img/swiper6.jpg")},
      ],
    };
  },
  async mounted(){
    let res = await getBanner(1);
    this.imgs = res.data.banners;
    var mySwiper = new Swiper("#swiperindex", {
      loop: true, // 循环模式选项
      // 如果需要分页器
      pagination: {
        el: ".swiper-pagination",
      },
    });
  },
};
</script>

<style lang="less">
#swipercom {
  width: 7.5rem;
  #swiperindex.swiper-container {
    width: 7.1rem;
    height: 3rem;
    border-radius: 0.15rem;
    .swiper-slide img {
      width: 100%;
      height: 100%;
    }
    .swiper-pagination-bullet-active {
      background-color: orangered;
    }
  }
}
</style>