<template>
  <!-- 播放控制器 底部 -->
  <div class="playController">
    <div class="left" @click="showitem" >
      <img :src="playlist[playCurrentIndex].al.picUrl" alt="" />
      <div class="content">
        <div class="title">{{ playlist[playCurrentIndex].name }}</div>
        <div class="tips">横滑可以切换上下首哟~</div>
      </div>
    </div>
    <div class="right">
      <!-- 播放 -->
      <svg
        v-if="paused"
        class="icon"
        aria-hidden="true"
        @click="play"
      >
        <use xlink:href="#icon-zanting1"></use>
      </svg>
      <!-- 暂停 -->
      <svg v-else class="icon" aria-hidden="true" @click="play">
        <use xlink:href="#icon-bofang6"></use>
      </svg>
      <!-- 列表 -->
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-bofangliebiao"></use>
      </svg>
    </div>
    <!-- 组件 -->
    <play-music  :play="play" :paused="paused" @back="show=!show" v-show="show" :playDetail="playlist[playCurrentIndex]"></play-music>
    <!-- 获取音乐 -->
    <audio
      ref="audio"
      :src="`https://music.163.com/song/media/outer/url?id=${playlist[playCurrentIndex].id}.mp3`"
    ></audio>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import playMusic from './playMusic.vue';
export default {
  components: { playMusic },
  data() {
    return {
        paused:true,
        show:false,
        id:''
    };
  },
  computed: {
    ...mapState(["playlist", "playCurrentIndex"]),
  },
  mounted() {
    // console.log(this.$refs.audio);
    // this.id=this.playlist[this.playCurrentIndex].id
    // console.log(this.id)
    this.$store.dispatch('reqLyric',{id:this.playlist[this.playCurrentIndex].id})
  },
  updated() {
    this.$store.dispatch('reqLyric',{id:this.playlist[this.playCurrentIndex].id})
    // console.log(this.playlist[this.playCurrentIndex].name)
  },
  methods: {
    play() {
      //如果暂停点击则播放
      if (this.$refs.audio.paused) {
        this.paused =! this.paused
        this.$refs.audio.play();
        this.UpdateTime()
      } else {
        //否则暂停
        this.paused =! this.paused
        this.$refs.audio.pause();
        clearInterval(this.$store.state.id)
      }
    },
    UpdateTime() {
      this.$store.state.id = setInterval(()=>{
        this.$store.commit('setCurrentTime',this.$refs.audio.currentTime)
      },1000)
    },
    showitem(){
      this.show=!this.show
      // let v=document.querySelector('#musicswiper') ;
      // console.log(v)
    }
  },
};
</script>

<style lang="less" scoped>
.playController {
  background-color: #fff;
  width: 7.5rem;
  height: 1.2rem;
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #ccc;
  .left {
    display: flex;
    padding: 0 0.3rem;
    img {
      width: 0.8rem;
      height: 0.8rem;
      border-radius: 0.4rem;
    }
    .content {
      padding: 0 0.3rem;
      .tips {
        font-size: 0.2rem;
        color: #999;
        margin-top: 0.1rem;
      }
    }
  }
  .right {
    margin-right: 0.2rem;
    .icon {
      margin: 0 0.2rem;
      width: 0.6rem;
      height: 0.6rem;
      margin-top:0.1rem;
    }
  }
}
</style>