import { createStore } from 'vuex'
import {getLyric,phoneLogin,userDetail} from '@/api/index.js'
export default createStore({
  state: {
    playlist:[{
      name:"彩劵",
      id:1486513704,
      // id:'',
      al:{
        id:"96680121",
        name:"彩劵",
        picUrl:"http://p3.music.126.net/NP1Zg57UUbHNzAhw7n-LeA==/109951165386012146.jpg",
        pic:109951165386012140,
        pic_str:"109951165386012146",
      }
    }],
    playCurrentIndex:0,
    lyric:'',
    currentTime:0,
    intervalId:0,
    user:{
      isLogin:false,
      account:{
      },
      userDetail:{}
    }
    

    
  },
  getters:{
    lyricList:function(state){
      let arr = state.lyric.split(/\n/igs).map((item,i,arr)=>{
        let min=parseInt(item.slice(1,3));
        let sec=parseInt(item.slice(4,6));
        let mill=parseInt(item.slice(7,10));
        
        return {
          min,sec,mill,
          lyric:item.slice(11,item.length),
          content:item,
          time:mill+sec*1000+min*60*1000
        }
      })
      // console.log(arr)
      arr.forEach((item,i)=>{
        if(i==0){
          item.pre=0
        }else{
          item.pre=arr[i-1].time
        }
      })
      return arr
     }
  },
  mutations: {
    // 播放内容
    setPlaylist:function(state,value){
      state.playlist = value
    },
    // 播放歌单的哪首歌
    setPlayIndex(state,value){
      state.playCurrentIndex = value
    },
    setLyric(state,value){
      state.lyric= value
    },
    setCurrentTime(state,value){
      state.currentTime= value
    },
    //播放搜索的哪首歌
    pushPlaylist(state,value){
      state.playlist.push(value)
    },
    //修改用户登录状态
    setUser(state,value){
       state.user = value
    }
  },
  actions: {
      // let result = await getLyric(payload.id)  
      // console.log(result)
   async reqLyric(content,payload){
      // console.log(payload)
      let result = await getLyric(payload.id)
      content.commit('setLyric',result.data.lrc.lyric)
      // console.log(result)
    },
    async phoneLogin(content,payload){
      let result = await phoneLogin(payload.phone,payload.password)
      // console.log(payload)
      // console.log(result)
      if(result.data.code==200){
        content.state.user.isLogin = true;
        content.state.user.account = result.data.account;
        let userDetails = await userDetail(result.data.account.id)
        content.state.user.userDetail = userDetails.data;
        localStorage.userData = JSON.stringify(content.state.user)
        console.log(userDetails)
        content.commit('setUser',content.state.user)
      }
      return result
    }
  },
  modules: {
  }
})
