<template>
  <!-- 音乐播放详情页 旋转动画 歌词-->
  <div class="playMusic">
    <div
      class="bg"
      :style="{ backgroundImage: `url(${playDetail.al.picUrl})` }"
      @click="lyricsshow"
    ></div>
    <div class="playTop">
      <div class="back" @click="$emit('back')">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-prev"></use>
        </svg>
      </div>
      <div class="center">
        <div class="title">
          <!-- 跑马灯标题 scroll可以设置重复往返的滑动-->
          <marquee behavior="scroll" direction="left">{{
            playDetail.al.name
          }}</marquee>
        </div>
        <!-- <div class="anthor"></div> -->
      </div>
      <div class="share">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-fenxiang"></use>
        </svg>
      </div>
    </div>
    <!-- v-show="!isLyric" -->
    <div class="playContent" v-show="!lyrics" @click="lyricsshow">
      <img
        class="needle"
        :class="{ active: !paused }"
        src="@/assets/img/needle-ab.png"
        alt=""
      />
      <!-- 默认播放动画，暂停的时候停止动画 -->
      <img
        class="disc Rotation"
        :class="{pause:paused}"
        src="@/assets/img/dist1.png"
        alt=""
      />
      <img
        class="playImg Rotation"
       :class="{pause:paused}"
        :src="playDetail.al.picUrl"
        alt=""
      />
    </div>
    <!-- v-show="isLyric" -->
    <div class="playLyric" v-show="lyrics" ref="playLyric" @click="lyricsshow">
      <p :class="{active:((currentTime*1000>=item.pre)&&currentTime*1000<item.time)}" v-for="(item,i) in $store.getters.lyricList" :key="i">{{item.lyric}}</p>
    </div>
    <div class="progress"></div>
    <div class="playFooter">
      <!-- 循环 -->
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-xunhuan"></use>
      </svg>
      <!-- 上一首 -->
      <svg class="icon" aria-hidden="true" @click="goPlay(-1)">
        <use xlink:href="#icon-shangyishoushangyige"></use>
      </svg>
      <!-- 播放暂停 -->
      <svg v-if="paused" class="icon play" aria-hidden="true" @click="play">
        <use xlink:href="#icon-bofang5"></use>
      </svg>
      <svg v-else class="icon play" aria-hidden="true" @click="play">
        <use xlink:href="#icon-zanting"></use>
      </svg>
      <!-- 下一首 -->
      <svg class="icon" aria-hidden="true" @click="goPlay(1)">
        <use xlink:href="#icon-xiayigexiayishou"></use>
      </svg>
      <!-- 列表 -->
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-bofangliebiao"></use>
      </svg>
    </div>
  </div>
</template>
<script>
import {watch} from '@vue/runtime-core';
import {mapState} from 'vuex'
export default {
  props: ["playDetail", "paused", "play"],
  data() {
    return {
      isLyric:true,
      lyrics:false,
    }
  },
  computed:{
    ...mapState(['lyric','currentTime','playlist','playCurrentIndex']),
  },
  watch:{
    currentTime(newValue){
      // console.log(newValue)
      let plocal= document.querySelector('p')
      let p = document.querySelector('p.active')
      // console.log(p)
      if(p){
        let offsetTop=p.offsetTop;
        this.$refs.playLyric.scrollTop=offsetTop;
      }
    }
  },
  methods: {
    goPlay(num){
      // console.log(num)
      // console.log(this.playlist)
      // console.log(this.playCurrentIndex)
      // 上下一首切换
      let index = this.playCurrentIndex+num
      if(index<0){
        index=this.playlist.length-1;
      }else if(index == this.playlist.length){
         index=0
      }
      this.$store.commit('setPlayIndex',index)
    },
    lyricsshow(){
      this.lyrics=!this.lyrics;
    }
  },
};
</script>

<style lang="less" scoped>
// 自定义动画
@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
.Rotation {
  transform: rotate(360deg);
  animation: rotation 15s linear infinite;
}
.pause{
    animation-play-state:paused;
    }
.playMusic {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  .bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-size: auto 100%; //宽 高
    background-position: center;
    filter: blur(60px); //模糊度
  }
  .playTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 7.5rem;
    height: 1.2rem;
    position: absolute;
    left: 0;
    top: 0;
    color: #fff;
    padding: 0 0.4rem;
    .icon {
      fill: #fff;
      width: 0.6rem;
      height: 0.6rem;
    }
    marquee {
      width: 5rem;
      flex: 1;
    }
  }
  .playContent {
    position: absolute;
    width: 7.5rem;
    height: 7.5rem;
    left: 0;
    top: 1.5rem;
    .needle {
      width: 2.5rem;
      height: 3rem;
      position: absolute;
      left: 3.2rem;
      transform-origin: 0.3rem 0; //旋转原点
      transform: rotate(-20deg);
      transition: all 1s;
      z-index: 10;
    }
    .needle.active {
      width: 2.5rem;
      height: 3rem;
      position: absolute;
      left: 3.5rem;
      transform-origin: 0.3rem 0; //旋转原点
      transform: rotate(10deg);
      transition: all 1s;
      z-index: 10;
    }
    .disc {
      position: absolute;
      width: 5.5rem;
      height: auto;
      left: calc(50% - 2.75rem); //居中
      top: 2rem;
    }
    .playImg {
      width: 3.6rem;
      height: 3.6rem;
      position: absolute;
      border-radius: 1.8rem;
      left: calc(50% - 1.8rem); //居中
      top: 3rem;
    }
  }
  .playFooter {
    width: 7.5rem;
    height: 1.5rem;
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.4rem;
    .icon {
      fill: #fff;
      width: 0.5rem;
      height: 0.5rem;
    }
    .play.icon {
      width: 0.9rem;
      height: 0.9rem;
    }
  }
  .playLyric{
    position: absolute;
    width: 7.5rem;
    height: 8rem;
    left: 0;
    top:calc(50% - 4.5rem);
    overflow: scroll;
    text-align: center;
    // color: #d9d6c3;
    color:#fff;
    padding: 0.2rem 0;
    .active{
      font-size:0.35rem;
      // color:#fff;
      color:orange;
    }
    p{
      margin-top:0.3rem;
    }
  }
}
</style>